.contactItem {
  display: flex;
  justify-content: space-between;
  background-color: #EAFDF8;
  padding: 12px;
  margin-top: 8px;
  border-radius: 8px;
  gap: 12px;
  list-style: none;
}

.contactDetails {
  color: #1d0d15;
  text-align: center;
}

.deleteButton {
  color: #EAFDF8;
  border: none;
  padding: 5px 10px;
  border-radius: 4px;
  cursor: pointer;
  background-color: #7C616C;
}

.deleteButton:hover {
  background-color: #D1B1CB;
}
